.ImageGalleryItem {
    border-radius: 2px;
    box-shadow: 0px 1px 3px 6px rgba(155, 247, 7, 0.2),
      0px 1px 1px 0px rgba(55, 245, 8, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}
   
.ImageGalleryItemImage {
    width: 100%;
    height: 260px;
    object-fit: cover;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.ImageGalleryItemImage:hover {
     transform: scale(1.03);
     cursor: zoom-in;
}